import React from "react";
import {
	Basket,
	BorderStyle,
	BoundingBox,
	WindowDesktop,
} from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import { Dropdown } from "react-bootstrap";
const ShopDash = () => {
	const navigate = useNavigate();
	return (
		<>
			<div className="col-6 col-md-4 ">
				{" "}
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`shop-categories`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<BoundingBox className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">
									Product Categories
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				{" "}
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`shop-products`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<Basket className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">
									Shop Products
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>{" "}
			<div className="col-6 col-md-4 ">
				{" "}
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`shop-entries`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<BorderStyle className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">
									Products Entries
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>{" "}
			<div className="col-6 col-md-4 ">
				{" "}
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`pos`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<WindowDesktop className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">
									Point Of Sale
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
		</>
	);
};

export default ShopDash;
