import React from "react";
import { CashStack, PersonBadge } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import { Dropdown } from "react-bootstrap";
const AdminDash = () => {
	const navigate = useNavigate();
	return (
		<>
			{" "}
			<div className="col-6 col-md-4 ">
				{" "}
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`staffs`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<PersonBadge className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">Staffs</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				{" "}
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`modes`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<CashStack className="dash-icon" />
								</div>
								<p className="text-center text-wrap dash-text  ">
									Payment Modes
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
		</>
	);
};

export default AdminDash;
